<template>
  <table className='table table-striped table-hover'>
        <thead>
            <tr>
                <th>Fecha</th>
                <th>Descripcion</th>
                <th>Monto</th>
                <th v-if="getPerfil==='admin'">Editar</th>
                <th v-if="getPerfil==='admin'">Eliminar</th>
            </tr>     
        </thead>
        <tbody>
 
            <tr  v-for="egr in getEgresosAll" :key="egr._id">
                <td>{{egr.fecha.slice(0,10)}}</td>
                <td>{{egr.descripcion}}</td>
                <td>{{egr.monto}}</td>

                <td>
                    <button
                        className='btn btn-secondary'
                        @click="setEgreso(egr)"
                        :disabled="getIsLoading"
                        v-if="getPerfil==='admin'"
                    >
                        Editar
                    </button>
                </td>
                <td>
                    <button
                        className='btn btn-danger'
                        @click="deleteEgreso(egr._id)"
                        :disabled="getIsLoading"
                        v-if="getPerfil==='admin'"
                    >
                        Eliminar
                    </button>
                </td>

            </tr>

        </tbody>
    
    </table>
    
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';

export default {
   
    computed:{
        ...mapGetters('egreso',['getEgresosAll','getIsLoading']),
        ...mapGetters('auth',['getPerfil'])
    },
    methods:{
        ...mapActions('egreso',['getEgresos','deleteEgreso']),
        ...mapMutations('egreso',['setEgreso'])
    },
    created(){
        this.getEgresos()
    }
}
</script>

